import { Component, OnInit } from '@angular/core';
import { AppState } from '../app.state';
import { Contact } from '../contact';
import { Groups } from "../groups";

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.css']
})
export class ContactsComponent implements OnInit {

  constructor(private appState: AppState) { }


  contacts: Array<Contact> = [];
  undefinedContacts: Array<Contact> = [];
  groupedContacts: Array<Contact> = [];

  groups = [];

  selectedContact = null;

  ngOnInit() {
    this.appState.getContactList().subscribe((contacts: Array<Contact>) => {
      this.undefinedContacts = contacts.filter((contact) => contact.group === Groups.Undefined);
      this.groupedContacts = contacts.filter((contact) => contact.group !== Groups.Undefined);
    });
    
    this.groups = this.appState.getGroups().filter((group) => group !== "Undefined");

    this.appState.getSelectedContact().subscribe((contact: Contact) => {
      this.selectedContact = contact;
    });

    this.appState.setInitialState();
  }

  setSelectedContact(contact: Contact) {
    this.appState.setSelectedContact(contact);
  }

  setGroup(group: string,) {
    this.appState.setGroup(this.selectedContact, group);
    this.appState.setSelectedContact(null);
  }
}
