import { Injectable } from '@angular/core';
import { Contact } from './contact';
import { Groups } from "./groups";
import { Subject, Observable } from "rxjs";
@Injectable({
  providedIn: 'root',
})
export class AppState {
  private contacts: Array<Contact> = [
    {
      fName: 'Ashish',
      lName: 'Kumar',
      email: 'akv',
      phNo: '1234567',
      group: Groups.Undefined,
    },
    {
      fName: 'Amit',
      lName: 'Kumar',
      email: 'amk',
      phNo: '432123',
      group: Groups.Undefined,
    },
  ];

  private selectedContact: Subject<Contact> =  new Subject<Contact>();

  private contactsSubject: Subject<Array<Contact>> = new Subject<Array<Contact>>();

  constructor() {
  }

  setInitialState() {
    this.contactsSubject.next(this.contacts);
  }

  getContactList(): Observable<Array<Contact>> {
    return this.contactsSubject.asObservable();
  }

  getGroups(): Array<String> {
    return Object.keys(Groups);
  }

  setSelectedContact(contact: Contact) {
    this.selectedContact.next(contact);
  }

  getSelectedContact(): Observable<Contact> {
    return this.selectedContact.asObservable();
  }

  setGroup(contact: Contact, group: string) {
    contact.group = Groups[group];
    this.contactsSubject.next(this.contacts); 
  }
}
